import { Col, Form, Row, Typography } from "antd";
import { useState, useEffect, useMemo, Fragment } from "react";
import Button from "../../../components/Buttons/Button";
import FiledCard from "../../../components/Cards/FiledCard";
import FormInput from "../../../components/Inputs/FormInput";
import BatchLabel from "../../../components/Labels/BatchLabel";
import FormLabel from "../../../components/Labels/FormLabel";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import {
  getLabelFromDateTime,
  getDateLabelFormat,
  convertBidExpiryDate,
  dateTimeToSingleValue,
} from "../../../../utilz/dates";
import Paper from "../../../components/Paper/Paper";

import FormDatePicker from "../../../components/Inputs/FormDatePicker";
import FormTimePicker from "../../../components/Inputs/FormTimePicker";
import FormTextArea from "../../../components/Inputs/FormTextArea";
import FormInputNumber from "../../../components/Inputs/FormInputNumber";
import Drawer from "../../../components/Modals/Drawer";
import FileDetails from "./FileDetails";
import CurrencySelector from "../../../components/Selectors/CurrencySelector";
import AuctionProductDetailCard from "./AuctionProductDetailCard";
import CargoTypeSelector from "../../../components/Selectors/CargoTypeSelector";
import { useDispatch, useSelector } from "react-redux";
import {
  setSpotBidSaveProgress,
  getSpotBidResultsAfterSubmitProgress,
  setSpotBidSaveStatusDefault,
} from "../../../../redux/auction/AuctionActions";
import moment from "moment/moment";
import "moment-timezone";
import AuctionTrimbleMapAPI from "../../../components/Maps/AuctionTrimbleMapAPI";
import {
  numberPadding,
  validateAddressAndConstruct,
} from "../../../../utilz/utilz";
import useShipmentCargoTypes from "../../../../redux/hooks/useShipmentCargoTypes";
import ViewAllStops from "./ViewAllStops";
import { getShipmentDetailsInProgress } from "../../../../redux/shipment/ShipmentActions";
import { setAppLoader } from "../../../../redux/common/CommonActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const AuctionDetailCardMap = ({
  spotBidData1,
  viewType,
  viewCardType,
  dataIndex,
  is12HoursFormat,
}) => {
  const convertToMoment = (date) => (date ? moment(date) : "");

  const [form] = Form.useForm();
  const [actionType, setActionType] = useState("UPLOAD");
  const [editMode, setEditMode] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [mapViewData, setMapViewData] = useState([]);
  const [mapViewDataCoordinates, setMapViewDataCoordinates] = useState([]);
  const [mapFullScreenStatus, setMapFullScreenStatus] = useState(false);
  const [loadingMap, setLoadingMap] = useState(true);
  const [bidExpiry, setBidExpiry] = useState("00:00:00");
  const [isDisabled, setDisabled] = useState(false);
  const [spotBidData, setSpotBidData] = useState({});
  const [buttonLoader, setButtonLoader] = useState(false);
  const [cargoTypeValue, setCargoTypeValue] = useState("");
  const { selectedCompany } = useSelector((state) => state.auth);
  const {
    insertSpotBidStatus,
    auctionSearchFilters,
    spotBidList,
    spotBidExportList,
  } = useSelector((state) => state.auction);
  const { shipmentCargoTypes } = useSelector((state) => state.common);
  const [fullScreenView, setFullScreenView] = useState(false);
  const dispatch = useDispatch();
  const cargoTypes = useShipmentCargoTypes();
  const { shipmentDetails } = useSelector((state) => state.shipment);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    setLoadingMap(true);
    const timer = setTimeout(() => {
      setLoadingMap(false);
    }, 200);
    checkBidExpired();
    setEditMode(false);
    getCargoTypeValue();
  }, [spotBidData]);

  useEffect(() => {
    setSpotBidData(spotBidData1);
  }, [dataIndex]);

  const checkBidExpired = () => {
    let duration = moment(spotBidData?.bidExpirtyDateTime).diff(
        moment
          .tz("America/New_York")
          .utc()
          .format("MM/DD/YYYY hh:mm:ss A"),
          "seconds"
      )
    if (duration > 0) {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  };

  const disabledTargetDate = () => {
    return  moment
    .tz("America/New_York")
    .utc();
  }

  const onCloseDrawer = () => {
    setDrawerOpen(false);
  };

  const setMapViewScreen = () => {
    setMapFullScreenStatus(!mapFullScreenStatus);
    viewCardType(viewType);
  };

  const fullViewMap = () => {
    setLoadingMap(true);
    const timer = setTimeout(() => {
      setLoadingMap(false);
    }, 200);
    setFullScreenView(!fullScreenView);
  };

  const btnActionType = (type) => {
    setActionType(type);
    setDrawerOpen(!drawerOpen);
  };

  const handleBidSubmit = (formValues) => {
    let params = {
      ...formValues,
      shipmentKey: spotBidData?.shipmentKey,
      customerKey: spotBidData?.customerKey,
      carrierKey: selectedCompany?.companyId,
      shipmentNo: spotBidData?.shipmentNumber,
      effectiveToDate: dateTimeToSingleValue(
        formValues?.effectiveToDate,
        formValues?.bidTime
      ),
    };
    dispatch(setSpotBidSaveProgress(params));
    setButtonLoader(true);
  };

  useEffect(() => {
    const param = {
      ...auctionSearchFilters,
      carrierKey: selectedCompany?.companyId,
    };
    if (insertSpotBidStatus?.success) {
      setSpotBidData(spotBidList?.data[dataIndex]);
      dispatch(getSpotBidResultsAfterSubmitProgress(param));
      dispatch(setSpotBidSaveStatusDefault({ data: 0 }));
    } else if (insertSpotBidStatus?.error) {
      setButtonLoader(false);
    }
  }, [insertSpotBidStatus]);

  let coordinates = [
    { latitude: "33.7697310", longitude: "-84.3772170" },
    { latitude: "33.7697310", longitude: "-84.3772170" },
  ];

  useEffect(() => {
    setEditMode(false);
    setButtonLoader(false);
  }, [spotBidList]);

  useEffect(() => {
    setSpotBidData(spotBidList?.data[dataIndex]);
    let tempArray = [];
    let coordinates = [];
    if (spotBidList?.data && spotBidList?.data?.length > 0) {
      coordinates = [];
      tempArray = [];

      const filterValue = spotBidExportList?.data?.filter((val) =>
        val?.bidStatus.includes("Open")
      );
      filterValue?.map((row, index) => {
        let coordi1 = { latitude: "33.7697310", longitude: "-84.3772170" };
        coordinates.push(coordi1);
        let coordi2 = { latitude: "33.7697310", longitude: "-84.3772170" };
        coordinates.push(coordi2);
        let tempObj1 = {
          city: row?.originCity,
          stateCode: row?.originState,
          countryName: row?.originCountryCode,
          countryCode: row?.originCountryCode,
          zipCode: row?.originZip,
          shipmentNumber: row?.shipmentNumber,
        };

        let tempObj2 = {
          city: row?.destinationCity,
          stateCode: row?.destinationState,
          countryName: row?.destinationCountryCode,
          countryCode: row?.destinationCountryCode,
          zipCode: row?.destinationZip,
          shipmentNumber: row?.shipmentNumber,
        };

        tempArray.push(tempObj1);
        tempArray.push(tempObj2);
      });
    }
    setMapViewData(tempArray);
    setMapViewDataCoordinates(coordinates);
  }, [spotBidList, spotBidData, spotBidExportList]);

  let transportationData = [
    {
      city: spotBidData?.originCity,
      stateCode: spotBidData?.originState,
      countryName: spotBidData?.originCountryCode,
      countryCode: spotBidData?.originCountryCode,
      zipCode: spotBidData?.originZip,
      shipmentNumber: spotBidData?.shipmentNumber,
    },
    {
      city: spotBidData?.destinationCity,
      stateCode: spotBidData?.destinationState,
      countryName: spotBidData?.destinationCountryCode,
      countryCode: spotBidData?.destinationCountryCode,
      zipCode: spotBidData?.destinationZip,
      shipmentNumber: spotBidData?.shipmentNumber,
    },
  ];

  const handleOpenBidButton = () => {
    setEditMode(true);
    form.setFieldsValue({
      equipmentTypeDetail: spotBidData?.equipmentTypeDetail,
      spotQuoteNumber: spotBidData?.spotQuoteNumber,
      cargoType: spotBidData?.cargoType,
      transitDays: spotBidData?.transitDays,
      rate: spotBidData?.cost,
      currencyCode: spotBidData?.currencyCode,
      bidComment: spotBidData?.bidComment,
      effectiveToDate: convertToMoment(spotBidData?.effectiveTo),
      bidTime: convertToMoment(spotBidData?.effectiveTo),
    });
  };

  useEffect(() => {
    let timerInterval = false;
    try {
      if (spotBidData?.bidExpirtyDateTime) {
        const expectedBidTime = convertBidExpiryDate(
          spotBidData?.bidExpirtyDateTime
        );

        if (expectedBidTime) {
          timerInterval = setInterval(() => {
            var duration = moment.duration(
              moment(expectedBidTime).diff(
                moment
                  .tz("America/New_York")
                  .utc()
                  .format("MM/DD/YYYY hh:mm:ss A")
              )
            );
            if (duration._milliseconds > 0) {
              var hours = parseInt(duration.asHours());
              var minutes = parseInt(duration.asMinutes()) % 60;
              var seconds = parseInt(duration.asSeconds()) % 60;
              setBidExpiry(
                `${numberPadding(hours)}:${numberPadding(
                  minutes
                )}:${numberPadding(seconds)}`
              );
            } else {
              if (timerInterval) {
                clearInterval(timerInterval);
              }
              setBidExpiry("00:00:00");
            }
            checkBidExpired();
          }, 1000);
        }
      }
    } catch (error) {}
    return () => {
      if (timerInterval) {
        clearInterval(timerInterval);
      }
    };
  }, [spotBidData?.bidExpirtyDateTime]);

  
  const getCargoTypeValue = () => {
    let value = shipmentCargoTypes?.data?.find(
      (obj) => obj?.codeId === spotBidData?.cargoType
    );
    setCargoTypeValue(value?.codeDesc);
  };

  useEffect(() => {
    if (spotBidData?.shipmentKey) {
      dispatch(
        getShipmentDetailsInProgress({ shipmentKey: spotBidData?.shipmentKey })
      );
      dispatch(setAppLoader(true));
    }
  }, [spotBidData]);

  useEffect(() => {
    if (shipmentDetails?.progressing === false) {
      dispatch(setAppLoader(false));
    }
  }, [shipmentDetails]);

  return (
    <Form
      onFinish={handleBidSubmit}
      form={form}
      initialValues={{ createdByType: 1 }}
    >
      <Paper className="p-16 border-muted mb-15 ">
        <Row>
          <Col sm={4}>
            <FiledCard
              title={t("translation:default.fields.shipmentNumber.placeholder")}
            >
              <PrimaryLabel className="text-highlight">
                {spotBidData?.shipmentNumber}
              </PrimaryLabel>
            </FiledCard>
          </Col>
          <Col sm={3}>
            <FiledCard title={t("translation:default.fields.bidStatus")}>
              <BatchLabel className={"mt-4"} type={spotBidData?.bidStatus}>
                {spotBidData?.bidStatus}
              </BatchLabel>
            </FiledCard>
          </Col>
          <Col sm={17}>
            <Row justify="end" className="m-0" gutter={5}>
              <Col>
                <Button
                  className="auction-btn"
                  disabled={shipmentDetails?.data?.length <= 2}
                  onClick={() => btnActionType("ViewAllStops")}
                >
                  {t("translation:auction.viewAllStops")}
                </Button>
              </Col>
              <Col>
                <Button
                  className="auction-btn"
                  onClick={() => btnActionType("PRODUCT")}
                >
                  {t("translation:auction.viewProductDetails")}
                </Button>
              </Col>
              <Col>
                <Button
                  className="auction-btn"
                  onClick={() => btnActionType("UPLOAD")}
                >
                  {t("translation:auction.viewUploadFiles")}
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
        <hr />
        <Row>
          {viewType === "CARD" && (
            <Fragment>
              {fullScreenView && (
                <Col sm={24} style={{ minHeight: "560px" }}>
                  <div className={`w-100 h-100 br-4`}>
                    {!loadingMap && spotBidData?.shipmentKey ? (
                      <AuctionTrimbleMapAPI
                        handleFullScreenClick={() => fullViewMap()}
                        hasEnabledFullScreen={mapFullScreenStatus}
                        coordinates={coordinates}
                        transportationList={transportationData}
                        drawLineOnMap={true}
                      />
                    ) : (
                      ""
                    )}
                  </div>
                </Col>
              )}
              {!fullScreenView && (
                <Col sm={24}>
                  <Row>
                    <Col sm={10}>
                      <Row className="mb-20">
                        <Col sm={6}>
                          <div className="field-card-container">
                            <Typography.Text className="field-card-title">
                              {t("translation:auction.shipmentDetails")}
                            </Typography.Text>
                          </div>
                        </Col>
                        <Col sm={18} className="pr-20">
                          <hr />
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={12}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.customerName.label"
                            )}
                          >
                            <PrimaryLabel className="text-highlight">
                              {spotBidData?.customerName}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={20}>
                          <FiledCard
                            title={t("translation:auction.shipperDetails")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.shipperName}
                            </PrimaryLabel>
                          </FiledCard>
                          <p className="m-0">
                            {validateAddressAndConstruct(
                              spotBidData?.originAddress,
                              spotBidData?.originCountry,
                              spotBidData?.originZip,
                              spotBidData?.originCity,
                              spotBidData?.originState
                            )}
                          </p>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={20}>
                          <FiledCard
                            title={t("translation:auction.consigneeDetails")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.consigneeName}
                            </PrimaryLabel>
                          </FiledCard>
                          <p className="m-0">
                            {validateAddressAndConstruct(
                              spotBidData?.destinationAddress,
                              spotBidData?.destinationCountry,
                              spotBidData?.destinationZip,
                              spotBidData?.destinationCity,
                              spotBidData?.destinationState
                            )}
                          </p>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={6}>
                          <FiledCard
                            title={t("translation:auction.frightClass")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.class}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard title={t("translation:auction.package")}>
                            <PrimaryLabel normal={true}>
                              {spotBidData?.packageUOMQty}{" "}
                              {spotBidData?.packageUOM}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard
                            title={t("translation:default.fields.pickupDate")}
                          >
                            <PrimaryLabel normal={true}>
                              {getLabelFromDateTime(
                                spotBidData?.expShipDate,
                                is12HoursFormat
                              )}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={6}>
                          <FiledCard
                            title={t("translation:default.fields.totalCube")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.cube}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.incoTerm.label"
                            )}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.incotermDesc
                                ? spotBidData?.incotermDesc
                                : "-"}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard
                            title={t("translation:auction.delieryDateAndTime")}
                          >
                            <PrimaryLabel normal={true}>
                              {getLabelFromDateTime(
                                spotBidData?.expDeliveryDate,
                                is12HoursFormat
                              )}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={12}>
                          <FiledCard
                            textClass={"mt-6"}
                            title={t("translation:default.fields.totalWeight")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.totalWeight}{" "}
                              {spotBidData?.weightUOM}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={12}>
                          <FiledCard
                            textClass={"mt-6"}
                            title={t(
                              "translation:auction.customerBIDExpiryDateTime"
                            )}
                          >
                            <PrimaryLabel normal={true}>
                              {getDateLabelFormat(
                                spotBidData?.bidExpirtyDateTime,
                                is12HoursFormat
                              )}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="mb-20">
                        <Col sm={24}>
                          <FiledCard
                            title={t("translation:default.fields.notes")}
                          >
                            <PrimaryLabel normal={true}>
                              {spotBidData?.shipmentNotes}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                    </Col>

                    <Col sm={14}>
                      <Row className="mb-15">
                        <Col
                          className={"flex-1"}
                          style={{ minHeight: "250px" }}
                        >
                          <div className={`w-100 h-100 br-4`}>
                            {!loadingMap && spotBidData?.shipmentKey && (
                              <AuctionTrimbleMapAPI
                                handleFullScreenClick={() => fullViewMap()}
                                hasEnabledFullScreen={mapFullScreenStatus}
                                coordinates={coordinates}
                                transportationList={transportationData}
                                drawLineOnMap={true}
                              />
                            )}
                          </div>
                        </Col>
                      </Row>
                      {!editMode && (
                        <Fragment>
                          <Row className="mb-15">
                            <Col sm={6}>
                              <div className="field-card-container">
                                <Typography.Text className="field-card-title">
                                  {t("translation:auction.BIDDetails")}
                                </Typography.Text>
                              </div>
                            </Col>
                            <Col sm={18}>
                              <hr />
                            </Col>
                          </Row>

                          <Row className="mb-15">
                            <Col sm={8}>
                              <FormLabel>
                                {t("translation:auction.equipmentTypeDetails")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.equipmentTypeDetail || ""}
                              </PrimaryLabel>
                            </Col>
                            <Col sm={8}>
                              <div className="mb-2">
                                <FormLabel>
                                  {t("translation:auction.BIDExpiryDateTime")}
                                </FormLabel>
                              </div>
                              <div className="mb-15">
                                <PrimaryLabel className="f-14 fw-600 text-dark">
                                  {getDateLabelFormat(
                                    spotBidData?.effectiveTo,
                                    is12HoursFormat
                                  )}
                                </PrimaryLabel>
                              </div>
                            </Col>

                            <Col sm={8}>
                              <FormLabel>
                                {t("translation:auction.spotQuoteNumber")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.spotQuoteNumber || ""}
                              </PrimaryLabel>
                            </Col>
                          </Row>

                          <Row className="mb-15" gutter={12}>
                            <Col sm={8}>
                              <FormLabel>
                                {t(
                                  "translation:default.fields.cargoType.label"
                                )}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.cargoType
                                  ? cargoTypeValue
                                  : "-NA-"}
                              </PrimaryLabel>
                            </Col>
                            <Col sm={8}>
                              <FormLabel>
                                {t("translation:auction.BIDSubmittedBy")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.userEmail || "-"}
                              </PrimaryLabel>
                            </Col>
                            <Col sm={8}>
                              <FormLabel>
                                {t("translation:auction.BIDComment")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.bidComment}
                              </PrimaryLabel>
                            </Col>
                          </Row>

                          <Row className="mb-15">
                            <Col sm={8} className={"mt-5"}>
                              <FormLabel>
                                {t("translation:default.fields.transitDays")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {spotBidData?.transitDays
                                  ? spotBidData?.transitDays
                                  : 0}
                              </PrimaryLabel>
                            </Col>
                            <Col sm={8}>
                              <FormLabel>
                                {t("translation:auction.BIDPriceAndCurrency")}
                              </FormLabel>
                              <br />
                              <PrimaryLabel className={"fw-600 f-14 text-dark"}>
                                {Number(spotBidData?.cost)?.toFixed(2)}
                                {spotBidData?.currencyDesc}
                              </PrimaryLabel>
                            </Col>
                            <Col sm={8}>
                              <FiledCard title={"Ceiling Rate"}>
                                <PrimaryLabel className="text-highlight blue-text">
                                  {Number(spotBidData?.targetPrice)?.toFixed(2)}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                          </Row>

                          <Row className="mb-15">
                            <Col sm={8} className="d-flex gap-half">
                              <Button
                                onClick={() => handleOpenBidButton()}
                                disabled={isDisabled}
                              >
                                {t("translation:default.fields.bid")}
                              </Button>
                              <FiledCard
                                title={t("translation:auction.BIDExpiry")}
                              >
                                <PrimaryLabel
                                  className={"fw-600 f-14 text-dark"}
                                >
                                  {bidExpiry}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                            <Col sm={8}>
                              <FiledCard title={"Spot BID Position"}>
                                <PrimaryLabel className="text-highlight">
                                  {spotBidData?.position}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                            <Col sm={8}></Col>
                          </Row>
                        </Fragment>
                      )}

                      {editMode && (
                        <Fragment>
                          <Row className="mb-15" gutter={8}>
                            <Col sm={8}>
                              <FormInput
                                label={t(
                                  "translation:auction.equipmentTypeDetails"
                                )}
                                editMode={editMode}
                                margin={"mb-14"}
                                name={"equipmentTypeDetail"}
                                placeholder={t(
                                  "translation:auction.enterEquipmentType"
                                )}
                                defaultValue={spotBidData?.equipmentTypeDetail}
                              />
                            </Col>
                            <Col sm={8}>
                              <Row gutter={6}>
                                <Col sm={12}>
                                  <FormDatePicker
                                    label={t("translation:auction.expiryDate")}
                                    name={"effectiveToDate"}
                                    editMode={editMode}
                                    margin={"mb-14"}
                                    inputLabelClasss="f-14 text-highlight"
                                    disabledTargetDate ={disabledTargetDate()}
                                    rules={[
                                      {
                                        required: true,
                                        message: t(
                                          "translation:auction.expiryDateIsRequired"
                                        ),
                                      },
                                    ]}
                                  />
                                </Col>
                                <Col sm={12}>
                                  <FormTimePicker
                                    use12Hours={is12HoursFormat}
                                    label={t("translation:auction.expiryTime")}
                                    name={"bidTime"}
                                    editMode={editMode}
                                    margin={"mb-14"}
                                    inputLabelClasss="f-14 text-highlight"
                                    rules={[
                                      {
                                        required: true,
                                        message: t(
                                          "translation:auction.expiryTimeIsRequired"
                                        ),
                                      },
                                    ]}
                                  />
                                </Col>
                              </Row>
                            </Col>
                            <Col sm={8}>
                              <FormInput
                                label={t("translation:auction.spotQuoteNumber")}
                                editMode={editMode}
                                margin={"mb-14"}
                                name={"spotQuoteNumber"}
                                placeholder={t(
                                  "translation:auction.enterQuoteNumber"
                                )}
                                defaultValue={spotBidData?.spotQuoteNumber}
                              />
                            </Col>
                          </Row>
                          <Row className="mb-15" gutter={8}>
                            <Col sm={16}>
                              <Row gutter={8} className="mb-15">
                                <Col sm={12}>
                                  <CargoTypeSelector
                                    name={"cargoType"}
                                    rules={[
                                      {
                                        required: true,
                                        message: `${t(
                                          "translation:default.fields.cargoType.label"
                                        )} is required`,
                                      },
                                    ]}
                                  />
                                </Col>
                                <Col sm={12}>
                                  <FormLabel>
                                    {t("translation:auction.BIDSubmittedBy")}
                                  </FormLabel>
                                  <br />
                                  <PrimaryLabel
                                    className={"fw-600 f-14 text-dark"}
                                  >
                                    {spotBidData?.userEmail
                                      ? spotBidData?.userEmail
                                      : "-"}
                                  </PrimaryLabel>
                                </Col>
                              </Row>
                              <Row gutter={8}>
                                <Col sm={12}>
                                  <FormInputNumber
                                    label={t(
                                      "translation:default.fields.transitDays"
                                    )}
                                    editMode={editMode}
                                    margin={"mb-14"}
                                    name={"transitDays"}
                                    defaultValue={spotBidData?.transitDays}
                                    rules={[
                                      {
                                        required: true,
                                        message: t(
                                          "translation:default.fields.transitDaysAreRequired"
                                        ),
                                      },
                                    ]}
                                  />
                                </Col>
                                <Col sm={12}>
                                  <Row gutter={6}>
                                    <Col sm={12}>
                                      <FormInput
                                        label={t(
                                          "translation:auction.BIDPrice"
                                        )}
                                        editMode={editMode}
                                        margin={"mb-14"}
                                        name={"rate"}
                                        placeholder={t(
                                          "translation:auction.enterPrice"
                                        )}
                                        defaultValue={spotBidData?.cost}
                                        rules={[
                                          {
                                            required: true,
                                            message: t(
                                              "translation:auction.priceIsRequired"
                                            ),
                                          },
                                          () => ({
                                            validator(_, value) {
                                              if (isNaN(value)) {
                                                return Promise.reject(
                                                  new Error(
                                                    t(
                                                      "translation:auction.pleaseEnterTheNumber"
                                                    )
                                                  )
                                                );
                                              } else {
                                                if (
                                                  spotBidData.cost == "" ||
                                                  spotBidData.cost == null ||
                                                  parseInt(spotBidData.cost) >=
                                                    parseInt(value)
                                                ) {
                                                  return Promise.resolve();
                                                } else {
                                                  return Promise.reject(
                                                    new Error(
                                                      t(
                                                        "translation:auction.pleaseEnterLowerTheNumber"
                                                      )
                                                    )
                                                  );
                                                }
                                              }
                                            },
                                          }),
                                        ]}
                                      />
                                    </Col>
                                    <Col sm={12}>
                                      <CurrencySelector
                                        label={t(
                                          "translation:auction.BIDCurrency"
                                        )}
                                        editMode={editMode}
                                        margin={"mb-14"}
                                        name={"currencyCode"}
                                        placeholder={t(
                                          "translation:default.fields.currency.label"
                                        )}
                                        value={spotBidData?.currencyCode}
                                      />
                                    </Col>
                                  </Row>
                                </Col>
                              </Row>
                            </Col>
                            <Col sm={8}>
                              <FormTextArea
                                label={t("translation:auction.BIDComment")}
                                editMode={editMode}
                                margin={"mb-14"}
                                name={"bidComment"}
                                placeholder={t(
                                  "translation:auction.enterComment"
                                )}
                                enableAutoFocus={false}
                                rows={6}
                                defaultValue={spotBidData?.bidComment}
                              />
                            </Col>
                          </Row>
                          <Row gutter={8}>
                            <Col sm={12} className="d-flex gap-half">
                              <Button
                                onClick={() => form.submit()}
                                loading={buttonLoader}
                              >
                                {t("translation:auction.submitBID")}
                              </Button>
                              <FiledCard
                                title={t("translation:auction.BIDExpiry")}
                              >
                                <PrimaryLabel
                                  className={"fw-600 f-14 text-dark"}
                                >
                                  {bidExpiry}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                            <Col sm={6}>
                              <FiledCard title={"Spot BID Position"}>
                                <PrimaryLabel className="text-highlight">
                                  {spotBidData?.position}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                            <Col sm={6}>
                              <FiledCard title={"Ceiling Rate"}>
                                <PrimaryLabel className="text-highlight blue-text">
                                  {Number(spotBidData?.targetPrice)?.toFixed(2)}
                                </PrimaryLabel>
                              </FiledCard>
                            </Col>
                          </Row>
                        </Fragment>
                      )}
                    </Col>
                  </Row>

                  <Row></Row>
                </Col>
              )}
            </Fragment>
          )}

          {viewType !== "CARD" && (
            <Col sm={24}>
              <Row className="mb-10">
                <Col className={"flex-1"} style={{ minHeight: "550px" }}>
                  <div className={`w-100 h-100 br-4 `}>
                    {!loadingMap && spotBidData?.shipmentKey ? (
                      <AuctionTrimbleMapAPI
                        handleFullScreenClick={() => setMapViewScreen()}
                        hasEnabledFullScreen={mapFullScreenStatus}
                        coordinates={mapViewDataCoordinates}
                        transportationList={mapViewData}
                        drawLineOnMap={false}
                      />
                    ) : (
                      ""
                    )}
                  </div>
                </Col>
              </Row>
            </Col>
          )}
        </Row>

        <Drawer
          visible={drawerOpen}
          onClose={onCloseDrawer}
          classFieldName={"auction-drawer-bg"}
          headerContents={
            <Row gutter={12}>
              <Col sm={8}>
                <div className="header-text">
                  {t("translation:default.fields.shipmentNumber.placeholder")} :{" "}
                  {spotBidData?.shipmentNumber}
                </div>
              </Col>
              {actionType === "ViewAllStops" && (
                <Col sm={6}>
                  <div className="header-text">
                    {`${t("translation:auction.noOfStops")} : `}
                    {shipmentDetails?.data?.transportationList?.length}
                  </div>
                </Col>
              )}
            </Row>
          }
          width={"75%"}
        >
          <div
            className="d-flex py-16 h-100"
            style={{ flexDirection: "column" }}
          >
            {actionType === "ViewAllStops" ? (
              <ViewAllStops />
            ) : actionType !== "PRODUCT" ? (
              <FileDetails
                actionType={actionType}
                shipmentKey={spotBidData?.shipmentKey}
                spotBidData={spotBidData}
              />
            ) : (
              <AuctionProductDetailCard
                actionType={actionType}
                shipmentKey={spotBidData?.shipmentKey}
                spotBidData={spotBidData}
              />
            )}
          </div>
        </Drawer>
      </Paper>
    </Form>
  );
};

export default AuctionDetailCardMap;
