import { useState, useMemo } from "react";
import { Col, Form, Row } from "antd";
import TextButton from "../../../components/Buttons/TextButton";
import { ReactComponent as ExportIcon } from "../../../../assets/icons/export-icon.svg";
import { ReactComponent as CardIcon } from "../../../../assets/icons/card.svg";
import { ReactComponent as MapViewIcon } from "../../../../assets/icons/map-view.svg";
import AuctionListFilter from "./AuctionListFilter";
import CommonModal from "../../../components/Modals/CommonModal";
import AuctionOrderExport from "./AuctionOrderExport";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const AuctionHeader = ({ viewType, viewCardType }) => {
  const [exportForm] = Form.useForm();
  const [showExportModal, setShowExportModal] = useState(false);
  const { spotBidList } = useSelector((state) => state.auction);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const exportData = () => {
    setShowExportModal(!showExportModal);
  };

  const submitExport = () => {
    exportForm.submit();
    setShowExportModal(!showExportModal);
  };

  return (
    <Row justify="space-between" className="py-15 px-14" align="middle">
      <Col className="flex-1">
        <Row align={"middle"} gutter={20}>
          <Col className="flex-1">
            <AuctionListFilter />
          </Col>
        </Row>
      </Col>
      <Col>
        <Row gutter={6}>
          <Col>
            <TextButton
              icon={<ExportIcon />}
              onClick={exportData}
              disabled={!(spotBidList?.data.length > 0)}
            >
              <label className="ml-5">{t('translation:default.buttons.export')}</label>
            </TextButton>
          </Col>
          <Col>
            <TextButton
              icon={viewType === "CARD" ? <MapViewIcon /> : <CardIcon />}
              onClick={() => viewCardType(viewType)}
              disabled={!(spotBidList?.data.length > 0)}
            >
              <label className="ml-5 pt-2">
                {viewType === "CARD" ? t('translation:default.fields.mapView') : t('translation:auction.card')}
              </label>
            </TextButton>
          </Col>
        </Row>
      </Col>
      <>
        <CommonModal
          className="vw-80"
          open={showExportModal}
          title={`${t('translation:default.fields.spotBid')} ${t('translation:default.buttons.export')}`}
          okText={t('translation:default.buttons.export')}
          cancelText={t('translation:default.buttons.close')}
          onClose={() => {
            setShowExportModal(!showExportModal);
          }}
          onCancel={() => {
            setShowExportModal(!showExportModal);
          }}
          onOk={() => submitExport()}
        >
          <AuctionOrderExport
            form={exportForm}
            showExportModal={showExportModal}
          />
        </CommonModal>
      </>
    </Row>
  );
};

export default AuctionHeader;
