import { useEffect, useMemo } from "react";
import { Col, Form, Row } from "antd";
import FilterContent from "./components/FilterContent";
import Button from "../../components/Buttons/Button";
import { AUCTION_DATE_FIELDS } from "../../../contants/Fields";
import { DEFAULT_DATE_FORMAT } from "../../../contants/common";
import { useDispatch } from "react-redux";
import {
  setSearchFilters,
  setSpotBidSearchResults,
} from "../../../redux/auction/AuctionActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';
import { useSelector } from "react-redux";

const AuctionFilter = () => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleFilterSubmit = (data) => {
    for (const key in data) {
      if (AUCTION_DATE_FIELDS.includes(key) && data[key]) {
        data[key] = data[key].format(DEFAULT_DATE_FORMAT);
      }
    }
    data.customerKey = data?.customerKey?.join(",");
    dispatch(setSearchFilters({ ...data, pageIndex: 1, pageSize: 10 }));
    dispatch(setSpotBidSearchResults({ data: [], progressing: true }));
  };

  const handleClear = () => {
    form.resetFields();
    form.setFieldsValue({
      openClose: "O",
    });
  };

  const { selectedCompany } = useSelector((state) => state.auth);
  useEffect(() => {
    if (selectedCompany && selectedCompany.companyId){
      form.resetFields();
      form.setFieldsValue({
        openClose: "O",
      });
      dispatch(setSpotBidSearchResults({ data: [], progressing: true }));
      dispatch(setSearchFilters({  
        customerKey: "",
        openClose: "O",
        equipmentType: 0,
        toNumber: "",
        shipmentCreateDateFrom: "",
        shipmentCreateDateTo: "",
        expectedShipmentDateFrom: "",
        expectedShipmentDateTo: "",
        approvalNumber: "",
        pageIndex: 1,
        pageSize: 10,
        orderByColumn: "",
        orderBySort: ""
      }));
    
    }
     
  }, [selectedCompany]);

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={handleFilterSubmit}
          form={form}
          initialValues={{ createdByType: 1, openClose: "O" }}
        >
          <FilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={handleClear}
            buttonType="button"
          >
            {t('translation:default.buttons.clear')}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t('translation:default.buttons.search')}
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default AuctionFilter;
