import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getSpotBidSearchResultsProgress,
  getSpotBidExportSearchResultsProgress,
} from "../../../redux/auction/AuctionActions";
import { valueToString } from "../../../utilz/utilz";
import AuctionDetailCard from "./components/AuctionDetailCard";

const AuctionContent = ({ viewType, viewCardType }) => {
  const { auctionSearchFilters, spotBidList, spotBidTotalCount } = useSelector(
    (state) => state.auction
  );
  const { selectedCompany } = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    getSpotBidList(auctionSearchFilters);
  }, [auctionSearchFilters]);

  const getSpotBidList = () => {
    const param = {
      ...auctionSearchFilters,
      carrierKey: selectedCompany?.companyId,
      orderByColumn: valueToString(auctionSearchFilters?.orderByColumn),
      shipperName: valueToString(auctionSearchFilters?.shipperName),
    };
    dispatch(getSpotBidSearchResultsProgress(param));
  };

  useEffect(() => {
    const param = {
      ...auctionSearchFilters,
      orderByColumn: valueToString(auctionSearchFilters?.orderByColumn),
      shipperName: valueToString(auctionSearchFilters?.shipperName),
      carrierKey: selectedCompany?.companyId,
      pageIndex: 1,
      pageSize: spotBidTotalCount?.totalCount,
    };
    dispatch(getSpotBidExportSearchResultsProgress(param));
  }, [spotBidTotalCount]);

  return (
    <>
      <AuctionDetailCard
        viewType={viewType}
        spotBidListData={spotBidList?.data}
        viewCardType={viewCardType}
      />
    </>
  );
};

export default AuctionContent;
