/* eslint-disable react/display-name */
import React from "react";
import ErrorBoundary from "../Common/ErrorBoundary";
import { Col, Row } from "antd";

const mapsWithErrorBoundary = (Component) => {
  return (props) => {
    return (
      <ErrorBoundary
        fallBackUI={
          <Row
            justify={"center"}
            className="h-100 align-items-center border-border br-5 p-30"
          >
            <Col>
              <p className="f-16 fw-600 text-danger text-center">
                WebGL is not available for this browser to view the map. Kindly
                update your browser to latest version and try to update your
                display Adapter.
                <br />
                <span className="fw-800">For more please contact Admin.</span>
              </p>
            </Col>
          </Row>
        }
      >
        <Component {...props} />
      </ErrorBoundary>
    );
  };
};

export default mapsWithErrorBoundary;
