import { Form, Input } from 'antd';
import React, { useEffect, useMemo, useState } from "react";
import { useSelector } from 'react-redux';
import { getShipperConsigneeResults } from '../../../../redux/auction/AuctionEffects';
import { getConsigneeSearchEffect } from '../../../../redux/shipment/ShipmentEffects';
import FormSelectBox from '../../Inputs/FormSelectBox';
import CustomCompanyOptions from '../components/CustomCompanyOptions';
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import i18n from 'i18next';

const ShipmentConsigneeSearchSelector = ({
  placeholder = i18n.t('translation:default.fields.transportationProvider.label'),
  label = i18n.t('translation:default.fields.transportationProvider.label'),
  rules = [],
  name,
  form = {},
  onChange = () => {},
  customerKey,
  userType,
  tooltip = false,
}) => {
  const { selectedCompany } = useSelector((state) => state.auth);
  const [customerKeyParam, setCustomerKeyParam] = useState('');
  const [params, setParams] = useState({});
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const formFieldValue = Form.useWatch(name, form);

  useEffect(() => {
    if (customerKey) {
      setCustomerKeyParam(customerKey);
    }
    if (userType === 'CARR') {
      setParams({
        customerKey: customerKeyParam,
        pageSize: 10,
        pageIndex: 1,
        consigneeFlag: 'Y',
        carrierKey: selectedCompany?.companyId,
      });
    } else {
      setParams({
        customerKey: customerKeyParam
          ? customerKeyParam
          : selectedCompany?.companyId,
        pageSize: 10,
        pageIndex: 1,
      });
    }
  }, [customerKey, selectedCompany, customerKeyParam]);

  const handleChange = (val) => {
    onChange(val ? val : {});
    form.setFieldsValue({ [name]: val?.name });
  };

  return (
    <>
      <FormSelectBox
        placeholder={placeholder}
        label={label}
        margin={'mb-0'}
        rules={rules}
        asyncFetchMode={true}
        asynchFuntion={
          userType === 'CARR'
            ? getShipperConsigneeResults
            : getConsigneeSearchEffect
        }
        defaultParams={params}
        onChange={handleChange}
        value={formFieldValue} //form.getFieldValue(name)
        accessors={{ label: 'name', value: 'shipRecKey' }}
        CustomOptionsComponent={CustomCompanyOptions}
        onClear={() => form.setFieldsValue({ [name]: undefined })}
        userType={userType}
        tooltip={tooltip}
      />
      <Form.Item name={name} className={'d-none'}>
        <Input />
      </Form.Item>
    </>
  );
};

export default ShipmentConsigneeSearchSelector;
