import { Col, Form, Row, Table } from "antd";
import { useState, useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getShipmentProductDetailProgress } from "../../../../redux/auction/AuctionActions";
import { setAppLoader } from "../../../../redux/common/CommonActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const AuctionProductDetailCard = ({ shipmentKey }) => {
  const [form] = Form.useForm();
  const [editMode, setEditMode] = useState(false);
  const { shipmentProductDetails } = useSelector((state) => state.auction);
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    dispatch(setAppLoader(true));
    dispatch(getShipmentProductDetailProgress({ shipmentKey }));
  }, [shipmentKey]);

  useEffect(() => {
    dispatch(setAppLoader(false));
  }, [shipmentProductDetails]);

  const columns = [
    {
      title: t("translation:default.fields.productCode.label"),
      dataIndex: "productCode",
      render: (text) => <span>{text}</span>,
      width: "10%",
    },
    {
      title: t("translation:default.fields.productDesc.label"),
      dataIndex: "productDescription",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.quantity.label"),
      dataIndex: "quantity",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.unitMeasure"),
      dataIndex: "billQtyUom",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.billQty"),
      dataIndex: "billQty",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.packageBilledQuantityunitOfMeasure"),
      dataIndex: "billQtyUom",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.length"),
      dataIndex: "length",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.width"),
      dataIndex: "width",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.height"),
      dataIndex: "height",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.cube.label"),
      dataIndex: "cube",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.dimUnitOfMeasure.label"),
      dataIndex: "dimUOMDesc",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:auction.grossWeight"),
      dataIndex: "weight",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:auction.weightUnitOfMeasure"),
      dataIndex: "weightUOMDesc",
      render: (text) => <span>{text}</span>,
    },
  ];

  const data = shipmentProductDetails?.success
    ? shipmentProductDetails?.data
    : [];

  return (
    <Form>
      <div className="p-20">
        <Row className="align-items-center mb-15" justify="space-between">
          <Col>
            <h2 className="header-text m-0">
              <span className="primary-text f-18">
                {t("translation:default.fields.productDetails")}{" "}
              </span>
            </h2>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col sm={24}>
            <Table
              className="auction-product-detail"
              columns={columns}
              dataSource={data}
              bordered
              pagination={false}
            />
          </Col>
        </Row>
      </div>
    </Form>
  );
};

export default AuctionProductDetailCard;
