import { Col, Row, Form } from "antd";
import { useMemo } from "react";
import { useSelector } from "react-redux";
import ShipmentConsigneeSearchSelector from "../../../components/Selectors/AsynchSearch/ShipmentConsigneeSearchSelector";
import ShipmentShipperSearchSelector from "../../../components/Selectors/AsynchSearch/ShipmentShipperSearchSelector";
import CitySelector from "../../../components/Selectors/CitySelector";
import CountrySelector from "../../../components/Selectors/CountrySelector";
import PostalCodeInput from "../../../components/Selectors/PostalCodeInput";
import StateSelector from "../../../components/Selectors/StateSelector";
import { trimObjectValues } from "../../../../utilz/utilz";
import FormInput from "../../../components/Inputs/FormInput";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const OriginDestinationSearch = ({ form, type }) => {
  const roleType =
    type === 1
      ? { short: "O", full: "shipper", key: "origin", label: "Shipper" }
      : { short: "C", full: "consignee", key: "dest", label: "Consignee" };
  const countryCode = Form.useWatch(`${roleType.key}Country`, form);
  const stateCode = Form.useWatch(`${roleType.key}State`, form);
  const customerKey = form.getFieldValue("customerKey");
  const { selectedCompany } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleCompanyChange = ({
    countryCode,
    zip,
    state,
    city,
    name,
    shipRecKey,
  }) => {
    form.setFieldsValue({
      [`${roleType.key}Country`]: countryCode,
      [`${roleType.key}Zip`]: zip,
      [`${roleType.key}State`]: state,
      [`${roleType.key}City`]: city,
      [`${roleType.full}Name`]: name,
      [`${roleType.full}Name`]: shipRecKey,
    });
  };

  const handleZipDetails = ({ city, state: stateCode }) => {
    form.setFieldsValue(
      trimObjectValues({
        [`${roleType.key}City`]: city,
        [`${roleType.key}State`]: stateCode,
      })
    );
  };

  return (
    <Row className="pt-10">
      {console.log(customerKey?.join(","), " CUSTKEY")}
      <Col sm={24} className={"mb-16"}>
        {type === 1 ? (
          <ShipmentShipperSearchSelector
            label={t('translation:default.fields.shipperName.label')}
            placeholder={t('translation:default.fields.shipperName.placeholder')}
            form={form}
            name={`${roleType.full}Name`}
            onChange={handleCompanyChange}
            customerKey={customerKey?.join(",")}
            userType={"CARR"}
          />
        ) : (
          <ShipmentConsigneeSearchSelector
            label={t('translation:default.fields.consigneeName.label')}
            placeholder={t('translation:default.fields.consigneeName.placeholder')}
            form={form}
            name={`${roleType.full}Name`}
            onChange={handleCompanyChange}
            customerKey={customerKey?.join(",")}
            userType={"CARR"}
          />
        )}
        <FormInput className="d-none" name={`${roleType.full}Name`} />
      </Col>
      <Col sm={24}>
        <CountrySelector
          label={`${roleType.label} ${t('translation:default.fields.country.label')}`}
          name={`${roleType.key}${t('translation:default.fields.country.label')}`}
        />
      </Col>
      <Col sm={24} className={"mb-16"}>
        <PostalCodeInput
          editMode={true}
          label={`${roleType.label} ${t('translation:default.fields.postalCode.label')}`}
          placeholder={t('translation:default.fields.postalCode.label')}
          margin={"mb-0"}
          name={`${roleType.key}Zip`}
          handleZipDetails={handleZipDetails}
          countryCode={countryCode}
        />
      </Col>
      <Col sm={24}>
        <StateSelector
          label={`${roleType.label} ${t('translation:default.fields.state.label')}`}
          name={`${roleType.key}${t('translation:default.fields.state.label')}`}
          countryCode={countryCode}
        />
      </Col>
      <Col sm={24}>
        <CitySelector
          label={`${roleType.label} ${t('translation:default.fields.city.label')}`}
          countryCode={countryCode}
          stateCode={stateCode}
          name={`${roleType.key}${t('translation:default.fields.city.label')}`}
        />
      </Col>
    </Row>
  );
};

export default OriginDestinationSearch;
