import { Col, Row } from "antd";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import AuctionDetailCardMap from "./AuctionDetailCardMap";
import EmptyRecords from "../../../components/Common/EmptyRecords";
import AuctionMiniCard from "./AuctionMiniCard";
import ScrollPagination from "../../../components/Paginations/ScrollPagination";
import { setSearchFilters } from "../../../../redux/auction/AuctionActions";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";
import useIs12HoursTimeFormat from "../../../../redux/hooks/useIs12HoursTimeFormat";

const AuctionDetailCard = ({ viewType, spotBidListData, viewCardType }) => {
  const [selectedBid, setSelectBid] = useState(undefined);
  const [selectedId, setSelectedId] = useState(0);
  const [indexValueState, setIndexValueState] = useState(0);
  const [totalPageCount, setTotalPageCount] = useState(0);
  const dispatch = useDispatch();
  const { auctionSearchFilters, spotBidList, spotBidTotalCount } = useSelector(
    (state) => state.auction
  );

  const handleLoadMore = () => {
    if (spotBidList?.data?.length > 0) {
      dispatch(
        setSearchFilters({
          ...auctionSearchFilters,
          pageIndex: auctionSearchFilters.pageIndex + 1,
        })
      );
    }
  };

  useEffect(() => {
    if (spotBidList?.data && spotBidList?.data.length > 0) {
      setTotalPageCount(
        Math.floor(
          (spotBidTotalCount?.totalCount +
            (auctionSearchFilters.pageSize - 1)) /
            auctionSearchFilters.pageSize
        )
      );
    }
  }, [spotBidTotalCount]);

  const is12HoursFormat = useIs12HoursTimeFormat();
  return (
    <div className="pt-15 h-100">
      {spotBidListData && spotBidListData?.length > 0 ? (
        <Row gutter={12} className="h-100">
          <Col sm={8} className="h-100">
            <div className="h-100 d-flex flex-direction-column">
              <ScrollPagination
                className={`${
                  spotBidListData?.length > 0 ? "mb-15" : "pb-15"
                } h-100`}
                loadMore={handleLoadMore}
                hasMore={
                  spotBidList?.progressing
                    ? false
                    : auctionSearchFilters?.pageIndex < totalPageCount
                }
                isLoading={spotBidList?.progressing}
                scrollOffset={0}
              >
                {spotBidListData?.length &&
                  spotBidListData?.map((autionObj, index) => (
                    <AuctionMiniCard
                      data={autionObj}
                      keyIndex={index}
                      selectedBidData={(bidData) => {
                        setSelectBid(bidData);
                      }}
                      selectedBidID={(bidID) => {
                        setSelectedId(bidID);
                      }}
                      is12HoursFormat={is12HoursFormat}
                    />
                  ))}
              </ScrollPagination>
            </div>
          </Col>

          <Col
            sm={16}
            className={
              "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column auction-main-card"
            }
          >
            <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
              <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
                <div className="custom-scroll-bar overflow-x-hidden flex-1">
                  <AuctionDetailCardMap
                    spotBidData1={
                      selectedBid
                        ? selectedBid
                        : spotBidListData[indexValueState]
                    }
                    viewType={viewType}
                    viewCardType={viewCardType}
                    dataIndex={selectedId}
                    changedIndex={(indexValue) => {
                      setIndexValueState(indexValue);
                    }}
                    is12HoursFormat={is12HoursFormat}
                  />
                </div>
              </div>
            </div>
          </Col>
        </Row>
      ) : (
        <div className="custom-scroll-bar pb-15 h-100">
          <>
            {spotBidList?.progressing ? (
              <ContentSpinner height="auto" />
            ) : (
              <EmptyRecords />
            )}
          </>
        </div>
      )}
    </div>
  );
};

export default AuctionDetailCard;
