import { Row, Col } from "antd";
import moment from "moment";
import { useEffect } from "react";
import { useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSelectedSpotBid } from "../../../../redux/auction/AuctionActions";
import {
  convertBidExpiryDate,
  getLabelFromDateTime,
} from "../../../../utilz/dates";
import {
  numberPadding,
  validateAddressAndConstruct,
} from "../../../../utilz/utilz";
import FiledCard from "../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import Paper from "../../../components/Paper/Paper";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const AuctionMiniCard = ({
  data,
  keyIndex,
  selectedBidData,
  selectedBidID,
  is12HoursFormat,
}) => {
  const [bidExpiry, setBidExpiry] = useState("00:00:00");
  const [selectedId, setSelectedId] = useState(0);
  const [selectedSpotBidData, setSelectedSpotBidData] = useState({});
  const dispatch = useDispatch();
  const { selectedSpotBid, spotBidList } = useSelector(
    (state) => state.auction
  );
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    let timerInterval = false;
    try {
      if (data?.bidExpirtyDateTime) {
        const expectedBidTime = convertBidExpiryDate(data?.bidExpirtyDateTime);

        if (expectedBidTime) {
          timerInterval = setInterval(() => {
            var duration = moment.duration(
              moment(expectedBidTime).diff(
                moment.tz("America/New_York")
                .utc()
                .format("MM/DD/YYYY hh:mm:ss A")
              )
            );
            if (duration._milliseconds > 0) {
              var hours = parseInt(duration.asHours());
              var minutes = parseInt(duration.asMinutes()) % 60;
              var seconds = parseInt(duration.asSeconds()) % 60;
              setBidExpiry(
                `${numberPadding(hours)}:${numberPadding(
                  minutes
                )}:${numberPadding(seconds)}`
              );
            } else {
              if (timerInterval) {
                clearInterval(timerInterval);
              }
              setBidExpiry("00:00:00");
            }
          }, 1000);
        }
      }
    } catch (error) {}
    return () => {
      if (timerInterval) {
        clearInterval(timerInterval);
      }
    };
  }, [data?.bidExpirtyDateTime]);

  const handleClick = (event, data, key1) => {
    event.preventDefault();
    setSelectedId(key1);
    selectedBidData(data);
    dispatch(setSelectedSpotBid(data));
    selectedBidID(key1);
  };

  useEffect(() => {
    dispatch(setSelectedSpotBid(spotBidList?.data[0]));
    setSelectedSpotBidData(spotBidList?.data[0]);
  }, []);
  return (
    <Paper
      className={`shipment-card border-muted ${
        data?.shipmentKey === selectedSpotBid?.shipmentKey
          ? "auction-card-active"
          : ""
      }`}
    >
      <Row
        className="px-15 py-14"
        onClick={(e) => {
          handleClick(e, data, keyIndex);
        }}
        gutter={15}
      >
        <Col>
          <Row className="mb-15">
            <Col sm={8}>
              <FiledCard title={t("translation:auction.approvalNumber")}>
                <PrimaryLabel className="text-highlight">
                  {data?.approvalNumber}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={8}>
              <FiledCard
                title={t("translation:default.fields.shipmentNumber.label")}
              >
                <PrimaryLabel className="text-highlight">
                  {data?.shipmentNumber}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={8}>
              <FiledCard
                title={t("translation:default.fields.customerName.label")}
              >
                <PrimaryLabel className="text-highlight">
                  {data?.customerName}
                </PrimaryLabel>
              </FiledCard>
            </Col>
          </Row>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t("translation:default.fields.shipper")}>
                <PrimaryLabel normal={true}>{data?.shipperName}</PrimaryLabel>
              </FiledCard>
              <p className="m-0">
                {validateAddressAndConstruct(
                  data?.originAddress,
                  data?.originCountry,
                  data?.originZip,
                  data?.originCity,
                  data?.originState
                )}
              </p>
            </Col>
            <Col sm={12}>
              <FiledCard title={t("translation:default.fields.consignee")}>
                <PrimaryLabel normal={true}>{data?.consigneeName}</PrimaryLabel>
              </FiledCard>
              <p className="m-0">
                {validateAddressAndConstruct(
                  data?.destinationAddress,
                  data?.destinationCountry,
                  data?.destinationZip,
                  data?.destinationCity,
                  data?.destinationState
                )}
              </p>
            </Col>
          </Row>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t("translation:default.fields.totalWeight")}>
                <PrimaryLabel normal={true}>
                  {data?.totalWeight} {data?.weightUomCode}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={12}>
              <FiledCard title={t("translation:default.fields.totalDistance")}>
                <PrimaryLabel normal={true}>
                  {data?.distance} {data?.uom}
                </PrimaryLabel>
              </FiledCard>
            </Col>
          </Row>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t("translation:auction.BIDExpiryDateTime")}>
                <PrimaryLabel normal={true}>
                  {getLabelFromDateTime(data?.effectiveTo, is12HoursFormat)}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={12}>
              <FiledCard title={t("translation:auction.BIDExpiry")}>
                <PrimaryLabel
                  normal={true}
                  className={`${
                    bidExpiry === "00:00:00" ? "text-danger" : "text-success"
                  }`}
                >
                  {bidExpiry}
                </PrimaryLabel>
              </FiledCard>
            </Col>
          </Row>
        </Col>
      </Row>
    </Paper>
  );
};

export default AuctionMiniCard;
