import React, { useState, useMemo } from "react";
import WithFilter from "../../screens/common/WithFilter";
import AuctionContent from "../../screens/AuctionScreens/AuctionContent";
import AuctionFilter from "../../screens/AuctionScreens/AuctionFilter";
import AuctionHeader from "../../screens/AuctionScreens/components/AuctionHeader";
import AuctionFooter from "../../screens/AuctionScreens/components/AuctionFooter";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const AuctionPage = () => {
  const [viewType, setViewType] = useState("CARD");

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const viewCardType = (type) => {
    if (type === "CARD") {
      setViewType("MAP");
    } else {
      setViewType("CARD");
    }
  };

  return (
    <WithFilter
      headerContent={
        <AuctionHeader viewType={viewType} viewCardType={viewCardType} />
      }
      filterTitle={t('translation:default.buttons.search')}
      filterElement={<AuctionFilter />}
      contentElement={
        <AuctionContent viewType={viewType} viewCardType={viewCardType} />
      }
      pageTitle={t('translation:auction.title')}
      footerContent={<AuctionFooter />}
    />
  );
};

export default AuctionPage;
