import { Col, Row } from "antd";
import VerticalLine from "../../../components/Bars/VerticalLine";
import FooterContentCard from "../../common/FooterContentCard";
import { ReactComponent as FilesIcon } from "../../../../assets/icons/files-records.svg";
import { ReactComponent as OpenboxIcon } from "../../../../assets/icons/open-box.svg";
import { ReactComponent as WarningIcon } from "../../../../assets/icons/alert-exclamtion.svg";
import { ReactComponent as AlertClockIcon } from "../../../../assets/icons/alert-with-clock.svg";
import { getSpotBidFooterValues } from "../../../../redux/auction/AuctionEffects";
import { useSelector } from "react-redux";
import { useState, useMemo } from "react";
import { useEffect } from "react";
import { valueToString } from "../../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const AuctionFooter = () => {
  const [footerData, setFooterData] = useState({});
  const { auctionSearchFilters } = useSelector((state) => state.auction);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (selectedCompany && selectedCompany.companyId) {
      getFooterValues();
    }
  }, [auctionSearchFilters]);

  const getFooterValues = async () => {
    try {
      const dataParam = {
        ...auctionSearchFilters,
        carrierKey: selectedCompany?.companyId,
        shipperName: valueToString(auctionSearchFilters?.shipperName),
      };
      const { data } = await getSpotBidFooterValues(dataParam);
      setFooterData(data[0]);
    } catch (error) {
      console.log(error);
      setFooterData({});
    }
  };

  return (
    <Row
      justify={"space-around"}
      className=" footer-container background-header px-20  py-12"
    >
      <Col>
        <FooterContentCard
          count={footerData?.totalBids}
          label={t('translation:auction.totalBIDS')}
          Icon={FilesIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={footerData?.openBids}
          label={t('translation:auction.openBIDS')}
          Icon={OpenboxIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={footerData?.expiredBids}
          label={t('translation:auction.expiredBIDS')}
          Icon={WarningIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={footerData?.expiringSoonBids}
          label={t('translation:auction.expiringSoonBIDS')}
          Icon={AlertClockIcon}
        />
      </Col>
    </Row>
  );
};

export default AuctionFooter;
