import { Col, Form, Row } from "antd";
import React, { useState, useMemo} from "react";
import IconButton from "../../../components/Buttons/IconButton";
import Paper from "../../../components/Paper/Paper";
import { ReactComponent as DownloadIcon } from "../../../../assets/icons/download-white.svg";
import FormInput from "../../../components/Inputs/FormInput";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import Button from "../../../components/Buttons/Button";
import SvgConverter from "../../../components/Common/SvgConverter";
import Files from "react-files";
import SaveCancelButtons from "../../../components/Buttons/SaveCancelButtons";
import { MODULES_ID } from "../../../../contants/common";
import { filter } from "lodash";
import HiddenFormInput from "../../../components/Inputs/HiddenFormInput";
import DocumentTypeSelector from "../../../components/Selectors/DocumentTypeSelector";
import FormTextArea from "../../../components/Inputs/FormTextArea";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const FileDetailsCard = ({
  isNew,
  fileDetails,
  handleSave,
  handleDelete,
  index,
  uploadedDocumentTypes,
  allowedExtentions,
  actionType,
  shipmentKey,
}) => {
  const [editMode, setEditMode] = useState(Boolean(fileDetails.isNew));
  const [file, setFile] = useState(false);
  const [form] = Form.useForm();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const handleSubmit = (data) => {
    const formData = new FormData();
    formData.append("DocumentTitle", data?.documentTitle || "");
    formData.append("ReferenceNo", data?.referenceNo || "");
    formData.append("DocumentTypeCode", data?.docTypeCode || "");
    formData.append("DocumentDesc", data?.documentDesc || "");
    if (file) {
      formData.append("File", file);
    }

    // if (!isNew) {
    // }

    handleSave(formData, isNew);
  };

  const handleFileChange = (files) => {
    try {
      if (files.length > 0) {
        setFile(files[0]);
        form.setFieldsValue({ file: files[0].name });
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleFileError = (error) => {
    try {
      if (error) {
        form.setFields([
          {
            name: "file",
            errors: [error.message],
          },
        ]);
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handleReset = () => {
    form.setFieldsValue({
      docTypeCode: fileDetails.fileType,
      referenceNo: fileDetails.referenceNo,
      documentTitle: fileDetails.fileComments,
      documentDesc: fileDetails.fileDescription,
    });
    setEditMode(false);
  };

  const fileName = Form.useWatch("file", form);
  const documentDesc = Form.useWatch("documentDesc", form);

  return (
    <Form
      form={form}
      onFinish={handleSubmit}
      initialValues={fileDetails}
      {...{
        initialValues: fileDetails?.fileGuid
          ? {
              docTypeCode: fileDetails.fileType,
              referenceNo: fileDetails.referenceNo,
              documentTitle: fileDetails.fileComments,
              documentDesc: fileDetails.fileDescription,
              file: fileDetails.file,
            }
          : {},
      }}
    >
      <Paper className="border-muted mb-16">
        <div className="border-bottom-muted px-16 pt-8 pb-10">
          <Row justify="space-between" align="middle">
            <Col>
              <Row gutter={5} align={"middle"}>
                <Col>
                  <SecondaryLabel className="f-12">{t('translation:default.documentManager.fileName')} </SecondaryLabel>
                </Col>

                <Col>
                  {editMode && allowedExtentions && (
                    <Files
                      className="files-dropzone"
                      onChange={handleFileChange}
                      onError={handleFileError}
                      accepts={allowedExtentions}
                      multiple={false}
                      // maxFileSize={10000000}
                      minFileSize={0}
                      clickable
                    >
                      <Button>{t("translation:default.documentManager.chooseFile")}</Button>
                    </Files>
                  )}
                  <HiddenFormInput
                    name="file"
                    rules={[
                      { required: true, message: t('translation:default.documentManager.chooseFileMessage') },
                    ]}
                  />
                </Col>

                <Col>
                  <label className="f-14 fw-600">{fileName}</label>
                </Col>
              </Row>
            </Col>
            <Col>
              <Row gutter={16} align={"middle"}>
                {fileDetails?.cloudUrl?.length > 0 && (
                  <Col>
                    <a
                      target={"_blank"}
                      href={fileDetails?.cloudUrl?.[0]}
                      rel="noreferrer"
                    >
                      <IconButton>
                        <SvgConverter Icon={DownloadIcon} color={"header"} />
                      </IconButton>
                    </a>
                  </Col>
                )}
                {shipmentKey && isNew ? (
                  <Col className="lh-0">
                    <SaveCancelButtons
                      editMode={editMode}
                      setEditMode={setEditMode}
                      onCancel={() => {
                        if (isNew) {
                          handleDelete(index, isNew);
                        } else {
                          handleReset();
                        }
                      }}
                      handleRemove={() => handleDelete(index, isNew)}
                    />
                  </Col>
                ) : (
                  ""
                )}
              </Row>
            </Col>
          </Row>
        </div>
        <Row className="p-16" gutter={16}>
          <Col sm={6}>
            <Row>
              <Col sm={24}>
                <DocumentTypeSelector
                  editMode={editMode ? Boolean(isNew) : false}
                  margin={"mb-16"}
                  name={"docTypeCode"}
                  moduleId={MODULES_ID.SHIPMENT}
                  rules={[
                    { required: true, message: t('translation:default.documentManager.documentManager.message') },
                    {
                      validator: (e, value) => {
                        return !isNew ||
                          filter(uploadedDocumentTypes, (val) => val === value)
                            .length === 0
                          ? Promise.resolve()
                          : Promise.reject();
                      },
                      message: `${documentDesc} is already used.`,
                    },
                  ]}
                  onChange={({ label }) =>
                    form.setFieldsValue({ documentDesc: label })
                  }
                />
              </Col>
            </Row>
          </Col>

          <Col sm={18}>
            <Row>
              <Col sm={24}>
                <FormTextArea
                  label={t('translation:default.documentManager.fileComments.label')}
                  editMode={editMode}
                  margin={"mb-0"}
                  name={"documentTitle"}
                  rows="2"
                />
              </Col>
            </Row>
          </Col>
        </Row>
      </Paper>
      <HiddenFormInput name="documentDesc" />
    </Form>
  );
};
export default FileDetailsCard;
