import React, { useEffect, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Form, Row } from "antd";
import _, { cloneDeep, find, keyBy, omitBy } from "lodash";
import FilterCard from "../../../components/Cards/FilterCard";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import { AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS } from "../../../../contants/AuctionSpotBidEnum";
import { exportAuctionOrder } from "../../../../utilz/exportToExcel";
import { getSpotBidExportSearchResultsProgress } from "../../../../redux/auction/AuctionActions";
import { valueToString } from "../../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';


const AuctionOrderExport = ({ form, exportType = "", showExportModal }) => {
  const [auctionOrderInformationAll, setAuctionOrderInformationAll] =
    useState(true);
  const dispatch = useDispatch();
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { auctionSearchFilters, spotBidExportList, spotBidTotalCount } =
    useSelector((state) => state.auction);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (form) {
      handleSelectAll(AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS, true);
    }
  }, [form]);

  const handleSelectAll = (data, value) => {
    data.forEach(({ name }) => {
      form.setFieldValue(name, value);
    });
  };

  useEffect(() => {
    if (showExportModal) {
      getSportBidOrderList();
    }
  }, [showExportModal]);

  const handleSubmit = (data) => {
    if (spotBidExportList?.data.length > 0) {
      let selectedFields = omitBy(data, (val) => !val);
      let purchaseOrderInfo = [];
      let allFields = keyBy(
        [...AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS],
        "name"
      );

      for (const key in allFields) {
        if (selectedFields[key] === true) {
          if (find(AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS, { name: key })) {
            purchaseOrderInfo.push(
              find(AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS, { name: key })
            );
          }
        }
      }
      generateExportValuesWithFields(
        cloneDeep(purchaseOrderInfo),
        spotBidExportList?.data
      );
    }
  };

  const generateExportValuesWithFields = (columns, data) => {
    let rows = [];
    data.forEach((row) => {
      let arr = [];
      columns.forEach(({ name }) => {
        if (Object.hasOwnProperty.call(row, name)) {
          if (
            name === "createdOn" ||
            name === "expShipDate" ||
            name === "expDeliveryDate"
          ) {
            let dateVal = new Date(row[name] + "Z");
            arr.push(dateVal);
          } else {
            arr.push(row[name]);
          }
        } else {
          arr.push("");
        }
      });

      rows.push(arr);
    });
    columns = columns.map(({ label }) => ({
      name: label.length > 0 ? label : "---",
    }));

    exportAuctionOrder(columns, rows);
  };

  const getSportBidOrderList = () => {
    const param = {
      ...auctionSearchFilters,
      orderByColumn: valueToString(auctionSearchFilters?.orderByColumn),
      shipperName: valueToString(auctionSearchFilters?.shipperName),
    };
    param["carrierKey"] = selectedCompany?.companyId;
    param["pageIndex"] = 1;
    param["pageSize"] = spotBidTotalCount?.totalCount;

    dispatch(getSpotBidExportSearchResultsProgress(param));
  };

  return (
    <Form
      onFinish={handleSubmit}
      className="h-100 custom-scroll-bar"
      form={form}
    >
      <FilterCard
        showBorder={true}
        title={
          <FormCheckBox
            checkBoxClass={"normal-checkbox"}
            label={t('translation:default.fields.selectAll')}
            className={"m-0"}
            checked={auctionOrderInformationAll}
            id="ASExportCB1"
            onChange={(e) => {
              setAuctionOrderInformationAll(e.target.checked);
              handleSelectAll(
                AUCTION_SPOT_BID_EXPORT_INFORMATION_FIELDS,
                e.target.checked
              );
            }}
          />
        }
        className={"mb-20"}
      >
        <Row className={"px-16 pt-15"}>
          <Col lg={6}>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.bid')}
                name={"shipmentKey"}
                className={"mb-15"}
                id="ASExportCB2"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipmentNumber.placeholder')}
                name={"shipmentNumber"}
                className={"mb-15"}
                id="ASExportCB2"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.customerName.label')}
                name={"customerName"}
                className={"mb-15"}
                id="ASExportCB3"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.multiStop')}
                name={"multiStop"}
                className={"mb-15"}
                id="ASExportCB4"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.freightClass.label')}
                name={"class"}
                className={"mb-15"}
                id="ASExportCB5"
              />
            </Col>

            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.notes')}
                name={"shipmentNotes"}
                className={"mb-15"}
                id="ASExportCB7"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.totalCube')}
                name={"cube"}
                className={"mb-15"}
                id="ASExportCB8"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.effectiveDueDate')}
                name={"effectiveTo"}
                className={"mb-15"}
                id="ASExportCB8"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.price')}
                name={"cost"}
                className={"mb-15"}
                id="ASExportCB8"
              />
            </Col>
          </Col>
          <Col lg={6}>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperName.label')}
                name={"shipperName"}
                className={"mb-15"}
                id="ASExportCB11"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperCountry.label')}
                name={"originCountryCode"}
                className={"mb-15"}
                id="ASExportCB12"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperPostalCode')}
                name={"originZip"}
                className={"mb-15"}
                id="ASExportCB13"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperStateProvience')}
                name={"originState"}
                className={"mb-15"}
                id="ASExportCB14"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperCity.label')}
                name={"originCity"}
                className={"mb-15"}
                id="ASExportCB15"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.shipperAddress')}
                name={"originAddress"}
                className={"mb-15"}
                id="ASExportCB24"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.emailAddress')}
                name={"userEmail"}
                className={"mb-15"}
                id="ASExportCB24"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.createdOn.shipmentCreatedOn')}
                name={"shipmentCreateDate"}
                className={"mb-15"}
                id="ASExportCB24"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.currencyName')}
                name={"currencyDesc"}
                className={"mb-15"}
                id="ASExportCB24"
              />
            </Col>
          </Col>
          <Col lg={6}>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.consigneeName.label')}
                name={"consigneeName"}
                className={"mb-15"}
                id="ASExportCB16"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.consigneeCountry.label')}
                name={"destinationCountryCode"}
                className={"mb-15"}
                id="ASExportCB17"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.consigneePostalCode.label')}
                name={"destinationZip"}
                className={"mb-15"}
                id="ASExportCB18"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:auction.consigneeStateProvience')}
                name={"destinationState"}
                className={"mb-15"}
                id="ASExportCB19"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.consigneeCity.label')}
                name={"destinationCity"}
                className={"mb-15"}
                id="ASExportCB20"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.consigneeAddress')}
                name={"destinationAddress"}
                className={"mb-15"}
                id="ASExportCB21"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.transitDays')}
                name={"transitDays"}
                className={"mb-15"}
                id="ASExportCB21"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.pickupDate')}
                name={"expShipDate"}
                className={"mb-15"}
                id="ASExportCB21"
              />
            </Col>
          </Col>
          <Col sm={6}>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.totalWeight')}
                name={"totalWeight"}
                className={"mb-15"}
                id="ASExportCB22"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.weightUOM')}
                name={"weightUomCode"}
                className={"mb-15"}
                id="ASExportCB23"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.packageQuantity')}
                name={"packageUOMQty"}
                className={"mb-15"}
                id="ASExportCB22"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.packageUOMQuantity')}
                name={"packageUomCode"}
                className={"mb-15"}
                id="ASExportCB23"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.incoTerm.label')}
                name={"incotermDesc"}
                className={"mb-15"}
                id="ASExportCB22"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.equipmentType.label')}
                name={"equipmentTypeDetail"}
                className={"mb-15"}
                id="ASExportCB23"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.cargoType.label')}
                name={"cargoType"}
                className={"mb-15"}
                id="ASExportCB23"
              />
            </Col>
            <Col lg={24}>
              <FormCheckBox
                checkBoxClass={"normal-checkbox"}
                label={t('translation:default.fields.deliveryDate.label')}
                name={"expDeliveryDate"}
                className={"mb-15"}
                id="ASExportCB21"
              />
            </Col>
          </Col>
        </Row>
      </FilterCard>
    </Form>
  );
};

export default AuctionOrderExport;
