import { Col, Row } from "antd";
import _, { remove } from "lodash";
import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { MODULES_ID, NOTIFICATION_TYPES } from "../../../../contants/common";
import { setAppLoader } from "../../../../redux/common/CommonActions";
import useDocsExtentions from "../../../../redux/hooks/useDocsExtentions";
import { getShipmentDocumentsListInProgress } from "../../../../redux/shipment/ShipmentActions";
import {
  shipmentFileDeleteEffect,
  shipmentFileUpdateEffect,
  shipmentFileUploadEffect,
} from "../../../../redux/shipment/ShipmentEffects";
import { triggerNotification } from "../../../../utilz/utilz";
import Button from "../../../components/Buttons/Button";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";
import FilesDetailsCard from "./FileDetailsCard";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const FileDetails = ({ shipmentKey, actionType, spotBidData }) => {
  const [documentsList, setDocumentsList] = useState([]);
  const { shipmentDetails, documentDetails } = useSelector(
    (state) => state.shipment
  );
  const { user } = useSelector((state) => state.auth);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const { purchaseOrderFilters } = useSelector((state) => state.purchaseOrder);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const dispatch = useDispatch();
  useEffect(() => {
    if (documentDetails.success) {
      setDocumentsList(
        documentDetails.data.map((row) => ({ ...row, renderKey: _.uniqueId() }))
      );
    }
  }, [documentDetails?.success]);
  useEffect(() => {
    if (actionType === "UPLOAD") {
      handleNewFile();
    }
  }, [actionType]);
  useEffect(() => {
    getDocumentList();
  }, [shipmentKey]);

  const getDocumentList = () => {
    let payload = {
      shipmentKey: shipmentKey,
      customerKey: spotBidData?.customerKey,
    };
    dispatch(getShipmentDocumentsListInProgress(payload));
  };

  const handleNewFile = () => {
    let data = _.cloneDeep(documentsList);
    data = [{ renderKey: _.uniqueId(), isNew: true }, ...data];
    setDocumentsList(data);
  };

  const handleDelete = async (index, isNew) => {
    try {
      let data = _.cloneDeep(documentsList);
      if (!isNew) {
        dispatch(setAppLoader(true));
        let payload = {
          shipmentKey: shipmentKey,
          documentFileName: data[index].fileGuid,
        };
        await shipmentFileDeleteEffect(payload);
        dispatch(setAppLoader(false));
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: t(
            "translation:default.documentManager.notificationDeleteSuccess"
          ),
        });
      }
      data.splice(index, 1);
      setDocumentsList(data);
    } catch (error) {
      dispatch(setAppLoader(false));
      console.log(error);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.documentManager.notificationDeleteError"),
      });
    }
  };

  const handleSave = async (formData, isNew) => {
    dispatch(setAppLoader(true));
    try {
      formData.append("CustomerKey", spotBidData?.customerKey);
      formData.append("ShipmentKey", shipmentKey);
      if (isNew) {
        formData.append("CreatedBy", user.data.id);
        await shipmentFileUploadEffect(formData);
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: t(
            "translation:default.documentManager.notificationUploadingSuccess"
          ),
        });
        dispatch(setAppLoader(false));
        getDocumentList();
      } else {
        formData.append("CreatedBy", user.data.id);
        await shipmentFileUpdateEffect(formData);
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: t(
            "translation:default.documentManager.notificationUpdatingSuccess"
          ),
        });
        dispatch(setAppLoader(false));
        getDocumentList();
      }
    } catch (error) {
      console.log(error);
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.documentManager.notificationDeleteError"),
      });
    }
  };

  const allowedExtentions = useDocsExtentions(MODULES_ID.SHIPMENT);

  const uploadedDocumentTypes = useMemo(
    () =>
      remove(
        documentsList.map(({ fileType }) => (fileType ? fileType : false)),
        (val) => Boolean(val)
      ),
    [documentsList]
  );
  return (
    <div className="p-16">
      <Row className="align-items-center mb-15" justify="space-between">
        <Col>
          <h2 className="header-text m-0">
            <span className="primary-text f-18">
              {t("translation:default.documentManager.title")}{" "}
            </span>
          </h2>
        </Col>
        <Col>
          <Row justify="end" gutter={16}>
            <Col>
              <Button onClick={handleNewFile}>
                {t("translation:default.documentManager.addNewFile")}
              </Button>
            </Col>
          </Row>
        </Col>
      </Row>
      {documentDetails.progressing ? (
        <ContentSpinner size={"large"} />
      ) : (
        <>
          {documentsList.length > 0 ? (
            documentsList.map((row, index) => (
              <FilesDetailsCard
                key={row.renderKey}
                fileDetails={row}
                handleDelete={handleDelete}
                handleSave={handleSave}
                isNew={row.isNew}
                index={index}
                uploadedDocumentTypes={uploadedDocumentTypes}
                allowedExtentions={allowedExtentions}
                actionType={actionType}
                shipmentKey={shipmentKey}
              />
            ))
          ) : (
            <>
              <div className="p-30 text-center">
                {t("translation:default.documentManager.noFoundText")}
              </div>
            </>
          )}
        </>
      )}
    </div>
  );
};
export default FileDetails;
