import { Collapse } from "antd";
import { useState, useMemo } from "react";
import LocationSearch from "./LocationSearch";
import SpotBidSearch from "./SpotBidSearch";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const { Panel } = Collapse;

const FilterContent = ({ form }) => {
  const [activeKey, setActiveKey] = useState("1");
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleTabClick = (keys) => {
    if (keys && keys[1]) {
      setActiveKey(keys[1]);
    } else {
      setActiveKey("");
    }
  };

  return (
    <Collapse
      ghost
      className="filter-collapse-container px-16"
      activeKey={activeKey}
      onChange={handleTabClick}
    >
      <Panel header={t('translation:claims.spotBidSearch')} key="1">
        <SpotBidSearch form={form} />
      </Panel>
      <Panel header={t('translation:claims.spotBidSearchOriginDestination')} key="2">
        <LocationSearch form={form} />
      </Panel>
    </Collapse>
  );
};

export default FilterContent;
